<template>
  <!--   -->
  <iSection>
      <template slot="title"></template>
      <iRow>
        <iCol :span="24/sectionInfo.length" v-for="(info, i) in sectionInfo" :key="i">
          <SectionItem>
            <img slot="img" :src="info.imgUrl" />
            <template slot="title">{{info.title}}</template>
            <template slot="desc">{{info.desc}}</template>
          </SectionItem>
        </iCol>
    </iRow>
  </iSection>
</template>

<script>
  import {Row, Col} from 'iview/src/components/grid'
  import {homeImgDir} from '@/utils/staticUrl'

  export default {
    name: 'homepage_first_section',
    components: {
      iRow: Row,
      iCol: Col,
      iSection: () => import(/* webpackChunkName: "homepage" */ './Section'),
      SectionItem: () => import(/* webpackChunkName: "homepage" */ './FirstSectionItem')
    },
    data () {
      return {
        sectionInfo: [
          {
            imgUrl: `${homeImgDir}/site_intro_map.png`,
            title: '',
            desc: ''
          },
          {
            imgUrl: `${homeImgDir}/site_intro_data.png`,
            title: '',
            desc: ''
          },
          {
            imgUrl: `${homeImgDir}/site_intro_service.png`,
            title: '',
            desc: ''
          },
          {
            imgUrl: `${homeImgDir}/site_intro_app.png`,
            title: '',
            desc: 'WebWebWeb'
          }
        ]
      }
    }
  }
</script>


